.colorWheel{
  flex-grow: 5;
  display: flex;
  position: relative;
}

.wheel{
  cursor: pointer;
  z-index: 1;
  position: relative;
  flex: 1;
  height: 100%;
  border-radius: 100%;
  outline: none;
  background-size: cover;
}

.handle{
  border-radius: 100%;
  background-color: white;
  position: absolute;
  transform: translate(-50%, -50%);
}

.saturationBar-level{
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  transform: translate(0, -50%);
  background-color: black;
}

.lightnessBar{
  right: 0;
}

.lightnessBar-level{
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  transform: translate(0, -50%);
  background-color: black;
}

